
import Blur from 'vux/src/components/blur/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { DEFAULT_IMG } from "@/config/def";
export default {
name: "Cd",
data() {
return {
cd: {
logo: DEFAULT_IMG,
headurl: "",
dissname: ""
}
};
},
created() {
this.$store
.dispatch("getCdList", this.$route.params.id)
.then(response => {
let _data = response.data;
var curr_cd = {};
if (_data.cdlist.length > 0) {
curr_cd = _data.cdlist[0]
}
this.cd = curr_cd

},
responce => {
this.$vux.toast.show({
type: "text",
position: "top",
text: ""
});
})
},
methods: {
backToHome(){
this.$router.push('/');
}
},
components: {
Blur,
Flexbox,
FlexboxItem
},
filters: {
listenCount: num => {
return Math.round(num / 1000) / 10 + ''
}
}
};




